        span 1
      h1 Tcnicas y mtodos de evaluacin de usabilidad
    p(data-aos="fade-right") Las tcnicas de evaluacin dentro del contexto de la usabilidad son una parte crtica del proceso de desarrollo de #[i software], debido a su naturaleza iterativa. Una evaluacin de usabilidad representa la mejor manera de poner un producto en manos de usuarios reales para observar si lo usan y cmo lo usan antes del lanzamiento del producto.
    .row.mb-3
      .col-12.bg-c2(data-aos="fade-down")
        .p-4
          .row.align-items-center
            .col-lg-auto.pe-0.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img1.svg', style="width:350px", alt='').m-auto
            .col.bg-c3
              .p-4
                p.mb-0 La aplicacin de diversas tcnicas de evaluacin permitir medir cuantitativa y cualitativamente el comportamiento, el rendimiento y la satisfaccin del usuario. Algunos proyectos de #[i software] abordan las pruebas de usabilidad, pero solo al final del ciclo de desarrollo.
    p.mb-0(data-aos="fade-right") Desafortunadamente, esto es demasiado tarde en el ciclo de desarrollo para incorporar cambios basados en los resultados de las pruebas de usabilidad. Incluso si se realizan cambios en el diseo del producto, la interfaz debe evaluarse nuevamente para garantizar que el producto cumpla con los puntos de referencia y los objetivos del producto final, afectando la calidad del #[i software]. De esta forma, con los siguientes mtodos se espera presentar las mejores prcticas en torno a la evaluacin de usabilidad para sistemas interactivos de #[i software].
    Separador
