        .row
          .col-12.bg-c4
            .p-4
              p.mb-0 Es un conjunto de mtodos basados en la inspeccin de una interfaz de usuario por parte de un grupo de evaluadores. Normalmente, esta inspeccin de usabilidad tiene como objetivo identificar problemas de usabilidad en el diseo, aunque algunos mtodos tambin se enfocan en aspectos como la interaccin y funcionalidad de un sistema completo.
      .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img1.png', style="width:380px", alt='').m-auto
    .row.mb-5
      .col-12.px-4.pb-4.px-lg-5.pb-lg-5.bg-c5(data-aos="fade-down")
        .row.justify-content-center
