                      i.fas.fa-quote-left
                      p.text-center #[b Estos mtodos tambin se orientan a la inspeccin de especificaciones de la interfaz de usuario que an no se han implementado necesariamente, lo que significa que este ejercicio se puede realizar al principio del ciclo de vida de la ingeniera de usabilidad.]
                      i.fas.fa-quote-right
                    .bloque-texto-d__autor 
                      p.mb-0.text-center - (Nielsen, 1994).
    .row.mb-4
