              h3 Evaluacin heurstica
    p(data-aos="fade-right") En la literatura se encuentra un sinnmero de definiciones y ejemplos acerca de los pasos de la evaluacin heurstica, por lo que en este componente se agrupan las mejores descripciones y buenas prcticas para su aplicacin.
    p(data-aos="fade-right") A los #[b 10 principios] generales de Jakob Nielsen para el diseo de interacciones se les llama #[b heursticas], porque son reglas generales y no pautas de usabilidad especficas, como se ver a continuacin:
    .row.bg-c7
      .col-12
        .px-5.pt-5
          p.mb-4(data-aos="fade-right") #[b.txt-c1 Paso 1. Visibilidad del estado del sistema.] El diseo siempre debe mantener informados a los usuarios sobre lo que est sucediendo, mediante la retroalimentacin adecuada dentro de un perodo de tiempo razonable. (Nielsen, 2020).
          .row.justify-content-center.mb-4
            .col-10.bg-c8(data-aos="fade-left")
              .p-4
                .row.align-items-center
                  .col-md-auto.mb-4.mb-md-0
                    figure
                      img(src='@/assets/curso/tema1/img7.svg', style="width:100px" alt='').m-auto
                  .col
                    p #[b Recomendacin]
                    p Cuando los usuarios conocen el estado actual del sistema, conocen de por s el resultado de sus interacciones anteriores y determinan los prximos pasos.
                    p Las interacciones predecibles generan confianza tanto en el producto como en la marca.
                    p.mb-0 Se recomienda leer en el material de apoyo el concepto de aprendibilidad.
          p.mb-4(data-aos="fade-right") Algunos consejos para la visibilidad del estado del sistema son:
          .row.align-items-center.mb-4
            .col-lg-8.order-2.order-lg-1(data-aos="fade-right")
              .row
                .col-12.bg-c11
                  .p-4
                    ul.lista-ul
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Comunicar claramente a los usuarios cul es el estado del sistema, no se debe tomar ninguna accin con consecuencias para los usuarios sin informarles.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Presentar comentarios al usuario lo ms rpido posible (idealmente, de inmediato).
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Generar confianza a travs de una comunicacin abierta y continua.
            .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
              figure
                img(src='@/assets/curso/tema1/img9.png', style="width:350px" alt='').m-auto
          p.mb-4(data-aos="fade-right") A continuacin, se comparten algunas prcticas:
          .row.justify-content-center.mb-5
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-right")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img12.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: buena prctica]
                p Los indicadores de #[b "Usted est aqu"] en los mapas de centros comerciales tienen que mostrar a las personas dnde se encuentran actualmente, para ayudarles a comprender a dnde ir a continuacin.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img2.png', style="width:450px" alt='')
                    figcaption Nota. Tomada de Nielsen Norman Group (2020).
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-left")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img13.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: mala prctica]
                p Es una pgina web de compras, y genera confusin porque no se selecciona la opcin del men cuando se navega por l. Hay que recordar que todo de la UI (#[i User Interface]) debe indicar en donde se encuentra el usuario.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img3.png', style="width:420px" alt='')
                    figcaption Nota. Tomada de Modroo (2021).
    .row.bg-c7
      .col-12
        .px-5.pt-5
          p.mb-4(data-aos="fade-right") #[b.txt-c1 Paso 2. Coincidencia entre el sistema y el mundo real.] El diseo debe hablar el idioma de los usuarios. Utilice palabras, frases y conceptos familiares para el usuario, en lugar de jerga interna. Siga las convenciones del mundo real, haciendo que la informacin aparezca en un orden lgico y natural. (Nielsen, 2020).
          .row.justify-content-center.mb-4
            .col-10.bg-c8(data-aos="fade-down")
              .p-4
                .row.align-items-center
                  .col-md-auto.mb-4.mb-md-0
                    figure
                      img(src='@/assets/curso/tema1/img7.svg', style="width:100px" alt='').m-auto
                  .col
                    p #[b Recomendacin]
                    p La forma en que debe disear depende en gran medida de sus usuarios especficos. Los trminos, conceptos, conos e imgenes que le parezcan perfectamente claros a usted y a su equipo de trabajo pueden resultarles desconocidos o confusos a sus usuarios.
                    p.mb-0 Cuando los controles u objetos de interaccin de un diseo siguen las convenciones del mundo real y corresponden a los resultados deseados (llamado mapeo natural), es ms fcil para los usuarios aprender y recordar cmo funciona la interfaz. Esto ayuda a crear una experiencia intuitiva.
          p.mb-4(data-aos="fade-right") Consejos para la coincidencia entre el sistema y el mundo real:
          .row.mb-4
            .col-lg-8.order-2.order-lg-1(data-aos="fade-right")
              .row.justify-content-end 
                .col-11.bg-c18
                  .py-2
                    .row.align-items-center 
                      .col-auto.pe-0.fig-p2
                        figure
                          img(src='@/assets/curso/tema1/img15.svg', style="width:50px" alt='').m-auto
                      .col.fig-p2
                        p.mb-0 Asegrate de que los usuarios puedan entender el significado sin tener que buscar la definicin de una palabra.
              .row.justify-content-end
                .col-11.bg-c19
                  .py-2
                    .row.align-items-center 
                      .col-auto.pe-0.fig-p2
                        figure
                          img(src='@/assets/curso/tema1/img16.svg', style="width:50px" alt='').m-auto
                      .col.fig-p2
                        p.mb-0 Nunca asumas que su comprensin o entendimiento de palabras o conceptos coincidir con la de sus usuarios.
              .row.justify-content-end
                .col-11.bg-c20
                  .py-2
                    .row.align-items-center 
                      .col-auto.pe-0.fig-p2
                        figure
                          img(src='@/assets/curso/tema1/img17.svg', style="width:50px" alt='').m-auto
                      .col.fig-p2
                        p.mb-0 La investigacin de usuarios te ayudar a descubrir la terminologa familiar de sus usuarios, as como su manera de ver las cosas en torno a conceptos importantes.
            .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
              figure
                img(src='@/assets/curso/tema1/img4.png', style="width:310px" alt='').m-auto
          p.mb-4(data-aos="fade-right") A continuacin, se comparten algunas prcticas:
          .row.justify-content-center.mb-5
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-right")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img12.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: buena prctica]
                p Cuando los controles de la estufa coinciden con la disposicin de los elementos calefactores, los usuarios pueden comprender rpidamente que los controles estn asociados con la distribucin de cada calefactor.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img5.png', style="width:450px" alt='')
                    figcaption Nota. Tomada de Nielsen Norman Group (2020).
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-left")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img13.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: mala prctica]
                p En una de las pginas ms visitadas de comprar en lnea, se refleja un error que puede causar confusin, y es en este caso, el botn #[b Cmpralo ya!] porque resalta ms, al ser ms grande que el botn de Aadir a la cesta, por lo que fcilmente puede conducir a error por parte del usuario.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img6.png', style="width:420px" alt='')
                    figcaption Nota. Tomada del blog prototypr.io.
    .row.bg-c14
      .col-12
        .px-5.pt-5
          p.mb-4(data-aos="fade-right") #[b.txt-c1 Paso 3. Control y libertad del usuario.] Los usuarios suelen realizar acciones por error. Necesitan una "salida de emergencia" claramente marcada para abandonar la accin no deseada sin tener que pasar por un proceso prolongado (Nielsen, 2020).
          .row.justify-content-center.mb-5
            .col-10.bg-c13(data-aos="fade-down")
              .p-4
                .row.align-items-center
                  .col-md-auto.mb-4.mb-md-0
                    figure
                      img(src='@/assets/curso/tema1/img7.svg', style="width:100px" alt='').m-auto
                  .col
                    p #[b Recomendacin]
                    p Cuando es fcil para las personas retroceder en un proceso o deshacer una accin, se fomenta una sensacin de libertad y confianza, es ms de credibilidad del sistema #[i software]. Las salidas permiten a los usuarios mantener el control del sistema y, de esta forma, evitar quedarse atascados o, lo que es peor, sentir frustracin.
          .row.align-items-center.mb-5
            .col-lg-8.order-2.order-lg-1(data-aos="fade-right")
              .row
                .col-12.bg-c11
                  .p-4
                    ul.lista-ul
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Comunicar claramente a los usuarios cul es el estado del sistema, no se debe tomar ninguna accin con consecuencias para los usuarios sin informarles.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Presentar comentarios al usuario lo ms rpido posible (idealmente, de inmediato).
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Generar confianza a travs de una comunicacin abierta y continua.
            .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
              figure
                img(src='@/assets/curso/tema1/img49.png', style="width:350px" alt='').m-auto
    .row.bg-c7
      .col-12
        .px-5.pt-5
          p.mb-4(data-aos="fade-right") #[b.txt-c1 Paso 4. Coherencia y estndares.] Los usuarios no deberan tener que preguntarse si diferentes palabras, situaciones o acciones significan lo mismo. Siga las convenciones de la plataforma y la industria (Nielsen, 2020).
          .row.justify-content-center.mb-4
            .col-10.bg-c8(data-aos="fade-down")
              .p-4
                .row.align-items-center
                  .col-md-auto.mb-4.mb-md-0
                    figure
                      img(src='@/assets/curso/tema1/img7.svg', style="width:100px" alt='').m-auto
                  .col
                    p #[b Recomendacin]
                    p Esta regla establece que las personas pasan la mayor parte de su tiempo utilizando productos digitales distintos a los suyos. Las experiencias de los usuarios con esos otros productos establecen sus expectativas. Perder o no mantener la coherencia puede aumentar la carga cognitiva de los usuarios al obligarlos a aprender algo nuevo.
          p.mb-4(data-aos="fade-right") A continuacin, se presentan algunos consejos:
          .row.align-items-center.mb-4
            .col-lg-8.order-2.order-lg-1(data-aos="fade-right")
              .row
                .col-12.bg-c11
                  .p-4
                    ul.lista-ul
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Mejorar la capacidad de aprendizaje manteniendo ambos tipos de coherencia: interna y externa.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Mantener la consistencia dentro de un solo producto o una familia de productos (consistencia interna).
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Seguir las convenciones establecidas de la industria (consistencia externa).
            .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
              figure
                img(src='@/assets/curso/tema1/img50.png', style="width:350px" alt='').m-auto
          p.mb-4(data-aos="fade-right") Algunas prcticas son:
          .row.justify-content-center.mb-5
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-right")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img12.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: buena prctica]
                p Los mostradores de facturacin suelen estar ubicados en la parte delantera de los hoteles. Esta consistencia cumple con las expectativas de los clientes.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img7.png', style="width:350px" alt='')
                    figcaption Nota. Tomada de Nielsen Norman Group (2020).
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-left")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img13.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: mala prctica]
                p Sucedi en una pgina de comercio, en la cual sitan el formulario de bsqueda en un rea nada comn para esta finalidad que es en la parte izquierda, justo antes del logotipo de la marca. En ese sentido, la recomendacin es ubicar los elementos dnde los usuarios esperan encontrarlos.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img8.png', style="width:420px" alt='')
                    figcaption Nota. Tomada de Modroo (2021).
    .row.bg-c7
      .col-12
        .px-5.pt-5
          p.mb-4(data-aos="fade-right") #[b.txt-c1 Paso 5. Prevencin de errores.] Los buenos mensajes de error son importantes, pero los mejores diseos evitan cuidadosamente que ocurran problemas en primer lugar. Elimine las condiciones propensas a errores o verifquelas y presente a los usuarios una opcin de confirmacin antes de que se comprometa con la accin (Nielsen, 2020).
          .row.justify-content-center.mb-4
            .col-10.bg-c8(data-aos="fade-down")
              .p-4
                .row.align-items-center
                  .col-md-auto.mb-4.mb-md-0
                    figure
                      img(src='@/assets/curso/tema1/img7.svg', style="width:100px" alt='').m-auto
                  .col
                    p #[b Recomendacin]
                    ul.lista-ul
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Hay dos tipos de errores: involuntario y errores conscientes.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Los involuntarios son errores inconscientes causados por la falta de atencin.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Los errores conscientes estn basados en un desajuste entre el modelo mental del usuario (punto de vista de ver las cosas) y el diseo.
          p.mb-4(data-aos="fade-right") Algunos consejos son:
          .row.align-items-center.mb-4
            .col-lg-8.order-2.order-lg-1(data-aos="fade-right")
              .row
                .col-12.bg-c11
                  .p-4
                    ul.lista-ul
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Priorizar tu esfuerzo: primero evitar errores de alto costo y luego pequeas frustraciones.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Evitar deslizamientos proporcionando restricciones tiles y buenos incumplimientos.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Evitar errores eliminando cargas de memoria, ayudando a deshacer y advirtiendo a sus usuarios.
            .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
              figure
                img(src='@/assets/curso/tema1/img9.png', style="width:350px" alt='').m-auto
          p.mb-4(data-aos="fade-right") A continuacin, se comparten algunas prcticas:
          .row.justify-content-center.mb-5
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-right")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img12.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: buena prctica]
                p Barandilla en carreteras, para este ejemplo se muestra una va con curvas con el objeto de evitar que los conductores se caigan de acantilados.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img10.png', style="width:350px" alt='')
                    figcaption Nota. Tomada de Nielsen Norman Group (2020).
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-left")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img13.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: mala prctica]
                p Sucede en un formulario web, en el que no se informa de las caractersticas que debe tener cada campo (aqu es donde cobran mucho valor el uso de los #[i placeholder]). Cosa que hace solo cuando el usuario no valida el formulario, y es ah donde se desconoca, por ejemplo, que el campo Contrasea debe tener un mnimo 8 caracteres.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img11.png', style="width:420px" alt='')
                    figcaption Nota. Tomada del blog interactius, (Modroo, 2019).
    .row.bg-c7
      .col-12
        .px-5.pt-5
          p.mb-4(data-aos="fade-right") #[b.txt-c1 Paso 6. Reconocimiento en lugar de recordar.] Minimice la carga de memoria del usuario haciendo visibles los elementos, acciones y opciones. El usuario no debera tener que recordar informacin de una parte de la interfaz a otra. La informacin requerida para utilizar el diseo (por ejemplo, etiquetas de campo o elementos de men) debe ser visible o fcilmente recuperable cuando sea necesario (Nielsen, 2020).
          .row.justify-content-center.mb-4
            .col-10.bg-c8(data-aos="fade-up")
              .p-4
                .row.align-items-center
                  .col-md-auto.mb-4.mb-md-0
                    figure
                      img(src='@/assets/curso/tema1/img7.svg', style="width:100px" alt='').m-auto
                  .col
                    p #[b Recomendacin]
                    p.mb-0 En la literatura se menciona que los seres humanos tienen una memoria limitada a corto plazo. De esta forma, para reducir la cantidad de esfuerzo cognitivo requerido por parte de los usuarios se desarrollan interfaces que promueven el reconocimiento.
          p.mb-4(data-aos="fade-right") Algunas prcticas son:
          .row.mb-4
            .col-lg.m-2(data-aos="fade-right")
              .row.h-100
                .col-auto.bg-c15.d-flex.align-items-center
                  .p-2
                    figure
                      img(src='@/assets/curso/tema1/img23.svg', style="width:70px" alt='').m-auto
                .col.bg-c16.d-flex.align-items-center
                  .p-4
                    p.mb-0 Permitir que la gente reconozca la informacin en la interfaz, en lugar de tener que recordarla.
            .col-lg.m-2(data-aos="fade-down")
              .row.h-100
                .col-auto.bg-c15.d-flex.align-items-center
                  .p-2
                    figure
                      img(src='@/assets/curso/tema1/img24.svg', style="width:70px" alt='').m-auto
                .col.bg-c16.d-flex.align-items-center
                  .p-4
                    p.mb-0 Ofrecer ayuda en contexto, en lugar de dar a los usuarios un tutorial largo para memorizar.
            .col-lg.m-2(data-aos="fade-left")
              .row.h-100
                .col-auto.bg-c15.d-flex.align-items-center
                  .p-2
                    figure
                      img(src='@/assets/curso/tema1/img25.svg', style="width:70px" alt='').m-auto
                .col.bg-c16.d-flex.align-items-center
                  .p-4
                    p.mb-0 Reducir la informacin que los usuarios deben recordar.
          p.mb-4(data-aos="fade-right") Algunas prcticas son:
          .row.justify-content-center.mb-5
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-right")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img12.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: buena prctica]
                p Para la mayora de las personas es ms fcil reconocer las capitales de los pases, en lugar de tener que recordarlas. Es ms probable que la gente responda correctamente a la pregunta: Lisboa es la capital de Portugal?, en lugar de Cul es la capital de Portugal?
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img12.png', style="width:350px" alt='')
                    figcaption Nota. Tomada de Nielsen Norman Group (2020).
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-left")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img13.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: mala prctica]
                p Se presenta en un sitio Web de comercio en el cual no se especifican claramente los conceptos ni los filtros a modo de resumen para la bsqueda o indexacin de algn producto. Obligando al usuario a revisar distintas reas de la pantalla con el fin de recordar qu haba buscado previamente.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img13.png', style="width:420px" alt='')
                    figcaption Nota. Tomada del blog interactius, (Modroo, 2019).
    .row.bg-c7
      .col-12
        .px-5.pt-5
          p.mb-4(data-aos="fade-right") #[b.txt-c1 Paso 7. Flexibilidad y eficiencia de uso.] Los accesos directos, ocultos para los usuarios novatos, pueden acelerar la interaccin para el usuario experto, de modo que el diseo pueda satisfacer tanto a los usuarios sin experiencia como a los experimentados. Permitir a los usuarios personalizar las acciones frecuentes (Nielsen, 2020)
          .row.justify-content-center.mb-4
            .col-10.bg-c8(data-aos="fade-down")
              .p-4
                .row.align-items-center
                  .col-md-auto.mb-4.mb-md-0
                    figure
                      img(src='@/assets/curso/tema1/img7.svg', style="width:100px" alt='').m-auto
                  .col
                    p #[b Recomendacin]
                    p.mb-0 Los procesos deben ser  flexibles para que se puedan llevar a cabo de diferentes formas, de modo que las personas puedan elegir el mtodo que les funcione.
          p.mb-4(data-aos="fade-right") Algunos consejos son:
          .row.align-items-center.mb-4
            .col-lg-9.order-2.order-lg-1(data-aos="fade-right")
              .row
                .col-12.bg-c11
                  .p-4
                    ul.lista-ul
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Proporcionar aceleradores como atajos de teclado y gestos tctiles.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Proporcionar personalizacin adaptando el contenido y la funcionalidad para usuarios individuales.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Permitir la personalizacin, para que los usuarios puedan hacer selecciones sobre cmo quieren que funcione el producto.
            .col-lg-3.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
              figure
                img(src='@/assets/curso/tema1/img14.png', style="width:260px" alt='').m-auto
          p.mb-4(data-aos="fade-right") A continuacin, se comparten algunas prcticas:
          .row.justify-content-center.mb-5
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-right")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img12.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: buena prctica]
                p Las rutas regulares se enumeran en los mapas, pero los lugareos con ms conocimiento del rea pueden tomar atajos.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img15.png', style="width:350px" alt='')
                    figcaption Nota. Tomada de Nielsen Norman Group (2020).
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-left")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img13.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: mala prctica]
                p Se encuentra una oportunidad de mejora en uno de los sitios web ms visitados para el conocimiento y uso de la lengua espaola, en el cual no se muestran sugerencias cuando se introduce una palabra, de esta forma obliga a introducir la palabra entera cada vez que se hace una bsqueda.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img16.png', style="width:420px" alt='')
                    figcaption Nota. Tomada del blog interactius, (Modroo, 2019).
    .row.bg-c7
      .col-12
        .px-5.pt-5
          p.mb-4(data-aos="fade-right") #[b.txt-c1 Paso 8. Diseo esttico y minimalista.] Las interfaces no deben contener informacin que sea irrelevante o que rara vez se necesite. Cada unidad adicional de informacin en una interfaz compite con las unidades de informacin relevantes y disminuye su visibilidad relativa (Nielsen, 2020).
          .row.justify-content-center.mb-4
            .col-10.bg-c8(data-aos="fade-down")
              .p-4
                .row.align-items-center
                  .col-md-auto.mb-4.mb-md-0
                    figure
                      img(src='@/assets/curso/tema1/img7.svg', style="width:100px" alt='').m-auto
                  .col
                    p #[b Recomendacin]
                    p.mb-0 Esta regla no significa tener que usar un diseo plano, se trata de asegurarse de mantener el contenido y el diseo visual enfocados en lo esencial. Asegurarse de tener la atencin total del usuario y no despistarlo de su objetivo, es decir, permitir que los elementos visuales de la interfaz sean compatibles con los objetivos primordiales del usuario.
          p.mb-4(data-aos="fade-right") Algunos consejos son:
          .row.align-items-center.mb-4
            .col-lg-9.order-2.order-lg-1(data-aos="fade-right")
              .row
                .col-12.bg-c11
                  .p-4
                    ul.lista-ul
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Mantener el contenido y el diseo visual de la interfaz de usuario enfocados en lo esencial.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | No permitir que los elementos innecesarios distraigan a los usuarios de la informacin que realmente necesitan.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        | Priorizar el contenido y las funciones para respaldar los objetivos principales.
            .col-lg-3.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
              figure
                img(src='@/assets/curso/tema1/img17.png', style="width:230px" alt='').m-auto
          
          p.mt-4(data-aos="fade-up") A continuacin, se comparten algunas prcticas:

          .row.justify-content-center.mb-5
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-right")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img12.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: buena prctica]
                p Una tetera con muchos adornos puede tener elementos decorativos excesivos que de cierta forma interfieren con la facilidad de uso, como un mango incmodo o difcil de lavar la boquilla.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img18.png', style="width:300px" alt='')
                    figcaption Nota. Tomada de Nielsen Norman Group (2020).
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-left")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img13.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: mala prctica]
                p En este caso, el sitio web tiene secciones muy grandes para anuncios que opacan el sentido de la pgina y que sin lugar a dudas compiten visualmente con el contenido relevante de la pgina.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img19.png', style="width:420px" alt='')
                    figcaption Nota. Tomada de Medium (2020).
    .row.bg-c7
      .col-12
        .px-5.pt-5
          p.mb-4(data-aos="fade-right") #[b.txt-c1 Paso 9. Ayudar a los usuarios a reconocer, diagnosticar y recuperarse de errores.] Los mensajes de error deben expresarse en un lenguaje sencillo (sin cdigos de error), indicar con precisin el problema y sugerir una solucin de manera constructiva (Nielsen, 2020).
          .row.justify-content-center.mb-4
            .col-10.bg-c8(data-aos="fade-down")
              .p-4
                .row.align-items-center
                  .col-md-auto.mb-4.mb-md-0
                    figure
                      img(src='@/assets/curso/tema1/img7.svg', style="width:100px" alt='').m-auto
                  .col
                    p #[b Recomendacin]
                    p.mb-0 Los mensajes, notificaciones o alertas de error tambin deben presentarse con tratamientos visuales que acompae y de soporte a los usuarios a notarlos y reconocerlos.
          p.mb-4(data-aos="fade-right") Algunos consejos son:
          .row.justify-content-center.mb-4
            .col-md-8.col-lg.bg-c17.m-2(data-aos="fade-right")
              .p-4
                p.mb-0 Utilizar elementos visuales de mensajes de error tradicionales, como texto rojo en negrita.
            .col-md-8.col-lg.bg-c17.m-2(data-aos="fade-up")
              .p-4
                p.mb-0 Ofrecer a los usuarios una solucin, como un atajo que pueda resolver el error de inmediato.
            .col-md-8.col-lg.bg-c17.m-2(data-aos="fade-left")
              .p-4
                p.mb-0 Indicar a los usuarios lo que sali mal en un lenguaje que entendern; evitar la jerga tcnica.
          p.mb-4(data-aos="fade-right") A continuacin, se comparten algunas prcticas:
          .row.justify-content-center.mb-5
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-right")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img12.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: buena prctica]
                p Las seales de sentido incorrecto en la carretera le hacen recordar a los conductores que se dirigen en la direccin incorrecta y les solicita que se detengan.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img20.png', style="width:300px" alt='')
                    figcaption Nota. Tomada de Nielsen Norman Group (2020).
            .col-10.col-md-8.col-lg.bg-c10.m-2(data-aos="fade-left")
              .p-4
                .row.align-items-center.mb-4
                  .col-auto.pe-0
                    figure
                      img(src='@/assets/curso/tema1/img13.svg', style="width:30px" alt='')
                  .col
                    p.mb-0 #[b Ejemplo: mala prctica]
                p Como ejemplo se presenta un sitio web donde en el proceso de compra de un tiquete de transporte de trenes en Europa, aparecen mltiples mensajes de error con una nomenclatura que el usuario difcilmente entender.
                .row.justify-content-center
                  .col-auto
                    figure
                      img(src='@/assets/curso/tema1/img21.png', style="width:500px" alt='')
                    figcaption Nota. Tomada de Medium (2020).
    .row.bg-c7
      .col-12
        .px-5.pt-5
          p.mb-4(data-aos="fade-right") #[b.txt-c1 Paso 10. Ayuda y documentacin.] Es mejor si el sistema no necesita ninguna explicacin adicional. Sin embargo, puede ser necesario proporcionar documentacin para ayudar a los usuarios a comprender cmo completar sus tareas (Nielsen, 2020).
          .row.justify-content-center.mb-4
            .col-10.bg-c8(data-aos="fade-down")
              .p-4
                .row.align-items-center
                  .col-md-auto.mb-4.mb-md-0
                    figure
                      img(src='@/assets/curso/tema1/img7.svg', style="width:100px" alt='').m-auto
                  .col
                    p #[b Recomendacin]
                    p.mb-0 Los mensajes, notificaciones o alertas de error tambin deben presentarse con tratamientos visuales que acompae y de soporte a los usuarios a notarlos y reconocerlos.
          p.mb-4(data-aos="fade-right") A continuacin, se comparten algunos consejos:
          .row.mb-4
            .col-lg-8.order-2.order-lg-1(data-aos="fade-right")
              .row.justify-content-end 
                .col-11.bg-c18(data-aos="fade-right")
                  .py-2
                    .row.align-items-center 
                      .col-auto.pe-0.fig-p2
                        figure
                          img(src='@/assets/curso/tema1/img27.svg', style="width:50px" alt='').m-auto
                      .col.fig-p2
                        p.mb-0 Asegrate de que la documentacin de ayuda sea fcil de buscar.
              .row.justify-content-end
                .col-11.bg-c19(data-aos="fade-up")
                  .py-2
                    .row.align-items-center 
                      .col-auto.pe-0.fig-p2
                        figure
                          img(src='@/assets/curso/tema1/img28.svg', style="width:50px" alt='').m-auto
                      .col.fig-p2
                        p.mb-0 Siempre que sea posible, presenta la documentacin en contexto justo en el momento que el usuario lo requiera.
              .row.justify-content-end
                .col-11.bg-c20(data-aos="fade-left")
                  .py-2
                    .row.align-items-center 
                      .col-auto.pe-0.fig-p2
                        figure
                          img(src='@/assets/curso/tema1/img29.svg', style="width:50px" alt='').m-auto
                      .col.fig-p2
                        p.mb-0 Enumera los pasos concretos a realizar.
            .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
              figure
                img(src='@/assets/curso/tema1/img22.png', style="width:300px" alt='').m-auto
          .row.mb-5
            .col-lg-6.mb-4.mb-lg-0(data-aos="fade-right")
              .bg-c10
                .p-4
                  .row.align-items-center.mb-4
                    .col-auto.pe-0
                      figure
                        img(src='@/assets/curso/tema1/img12.svg', style="width:30px" alt='')
                    .col
                      p.mb-0 #[b Ejemplo: buena prctica]
                  p Los quioscos  o puntos de referencia  de informacin en los aeropuertos son fcilmente reconocibles y por lo regular resuelven los problemas de los clientes en contexto y de manera inmediata.
                  .row.justify-content-center
                    .col-auto
                      figure
                        img(src='@/assets/curso/tema1/img23.png', style="width:400px" alt='')
                      figcaption Nota. Tomada de Nielsen Norman Group (2020).
            .col-lg-6(data-aos="fade-left")
              p En este punto no traemos una mala prctica, pero s indicamos, que cualquier sitio web o aplicacin #[i software] que no presente ninguna seccin de ayuda, documentacin de la compaa o incluso de contacto, posiblemente va a generar un desamparo e incertidumbre al usuario.
              p Finalmente, puedes buscar consultar en el material complementario la plantilla Evaluacin Heurstica para que puedas realizar sin ningn problema una evaluacin de este tipo a un sistema #[i software].
              figure.d-flex.justify-content-end 
                img(src='@/assets/curso/tema1/img24.png', style="width:400px" alt='')
    .row.mb-4
      .col-auto.bg-c6(data-aos="fade-down")
        .px-3.pb-0
          .row.align-items-end
            .col-auto.pe-0
              figure.fig-p1
                img(src='@/assets/curso/tema1/img5.svg', style="width:50px" alt='').m-auto
            .col
              h3 Recorrido cognitivo
    p(data-aos="fade-right") Tambin conocido como #[b #[i Cognitive Walkthrough]], es un mtodo de inspeccin de usabilidad enfocado en evaluar la facilidad de aprendizaje de la interfaz grfica de una aplicacin, principalmente a travs de la exploracin. Este mtodo se basa en la observacin de varios usuarios que buscan aprender a utilizar el #[i software] explorando sus posibilidades.
    p.mb-5(data-aos="fade-right") Los pasos para la aplicacin de este mtodo son:
    .row.bg-c21.mb-5
      .col-12
        .p-5
          .tarjeta.tarjeta--gris1(data-aos="fade-up")
            .p-4
              PasosB.color-acento-botones
                .row.align-items-center.m-1(titulo="Paso 1")
                  .col-lg-8.mb-4.mb-lg-0
                    p #[b Definicin de los datos necesarios para el recorrido]
                    p A continuacin, se presenta cmo identificar y caracterizar a los usuarios, disear prototipos y definir tareas para mejorar la interaccin usuario-sistema.
                    .row 
                      .col-12
                        .p-4.bg-c22
                          ul.lista-ul
                            li.d-flex
                              i.fas.fa-circle(style="color:#1682FB !important")
                              | Identificacin y caracterizacin de usuarios, enfatizando la importancia de documentar y entender al pblico objetivo.
                            li.d-flex
                              i.fas.fa-circle(style="color:#1682FB !important")
                              | Descripcin de prototipos, que pueden variar desde esquemas en papel hasta desarrollos ms elaborados, sin necesidad de completitud o detalle extremo.
                            li.d-flex
                              i.fas.fa-circle(style="color:#1682FB !important")
                              | Enumeracin de tareas especficas a realizar de forma secuencial u ordenada por el usuario.
                            li.d-flex
                              i.fas.fa-circle(style="color:#1682FB !important")
                              | Implementacin escrita de cada tarea, detallando las acciones del usuario y sus interacciones con el sistema para completar las tareas utilizando el prototipo.
                  .col-lg-4
                    figure
                      img(src='@/assets/curso/tema1/img25.png', style="width:350px" alt='').m-auto
                .row.align-items-center.m-1(titulo="Paso 2")
                  .col-lg-8.mb-4.mb-lg-0
                    p #[b Recorrer las acciones]
                    p El evaluador analiza la idoneidad de la interfaz para el usuario, basndose en su experiencia y conocimiento, siguiendo criterios de usabilidad establecidos por Toni Granollers (2019) mediante preguntas crticas sobre la experiencia de usuario.
                    .row 
                      .col-12
                        .p-4.bg-c22
                          ul.lista-ul
                            li.d-flex
                              i.fas.fa-circle(style="color:#1682FB !important")
                              | Son adecuadas las acciones disponibles de acuerdo con la experiencia y al conocimiento del usuario?
                            li.d-flex
                              i.fas.fa-circle(style="color:#1682FB !important")
                              | Percibirn los usuarios que est disponible la accin correcta? Esto se relaciona con la visibilidad y la comprensibilidad de las acciones en la interfaz. La discusin no es como es el funcionamiento del sitio, sino que si la accin existe o no.
                            li.d-flex
                              i.fas.fa-circle(style="color:#1682FB !important")
                              | Una vez encontrada la accin en la interfaz, asociarn estos usuarios la accin correcta al efecto que se alcanzar?
                            li.d-flex
                              i.fas.fa-circle(style="color:#1682FB !important")
                              | Una vez realizada la accin, entendern los usuarios la realimentacin del sistema?, tanto si la accin se ha realizado con xito como en el caso contrario.
                  .col-lg-4
                    figure
                      img(src='@/assets/curso/tema1/img26.png', style="width:350px" alt='').m-auto
                .row.align-items-center.m-1(titulo="Paso 2")
                  .col-lg-8.mb-4.mb-lg-0
                    p #[b Documentar los resultados]
                    p Cada evaluador anotar para cada accin las respuestas del sistema y sus observaciones.
                    .row 
                      .col-12
                        .p-4.bg-c22
                          ul.lista-ul
                            li.d-flex
                              i.fas.fa-circle(style="color:#1682FB !important")
                              | El documento debe incluir un anexo que es conocido como Usability Problem Report Sheet, en el material complementario lo podrs encontrar como plantilla reporte de usabilidad. En esta plantilla se detallan los aspectos negativos de la evaluacin, relacionndolos con un grado de severidad, logrando distinguir aquellos errores ms dainos de los que no lo son tanto.
                  .col-lg-4
                    figure
                      img(src='@/assets/curso/tema1/img27.png', style="width:350px" alt='').m-auto
    .row.mb-4
      .col-auto.bg-c6(data-aos="fade-down")
        .px-3.pb-0
          .row.align-items-end
            .col-auto.pe-0
              figure.fig-p1
                img(src='@/assets/curso/tema1/img5.svg', style="width:50px" alt='').m-auto
            .col
              h3 Inspeccin de estndares
    .row.align-items-center.mb-5
      .col-lg-8.order-2.order-lg-1(data-aos="fade-right")
        p Primero, se comienza por entender qu es un #[b estndar], el cual se define #[b como un requisito, regla o recomendacin basada en principios comprobados y, especialmente, en la prctica]. De esta manera, representa un consenso entre un grupo de profesionales oficialmente autorizados a nivel nacional e internacional. 
        p Es importante considerar la definicin de Norman, quien afirma que #[b los estndares son eternos, ya que una vez establecidos, simplifican y dominan las vidas de millones, incluso miles de millones de personas].
        .row
          .col-12.bg-c4
            .p-4
              p.mb-0 Esta tcnica asegura, hasta cierto punto, la conformidad con los estndares industriales. En dichas inspecciones, un profesional de la usabilidad, con amplio conocimiento del estndar, analiza los elementos del producto o aplicacin en cuestin para asegurar su uso y adaptacin al estndar industrial.
      .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img30.svg', style="width:400px" alt='').m-auto
    .row.justify-content-center.mb-5
      .col-lg-10.bg-c23(data-aos="fade-down")
        .p-4
          .row
            .col-12
              .p-4.bg-c24
                .row.align-items-center
                  .col-md-auto.mb-4.mb-md-0
                    figure
                      img(src='@/assets/curso/tema1/img31.svg', style="width:70px" alt='').m-auto
                  .col 
                    p.mb-0 Los productos de #[i software] diseados para un contexto social determinado deberan disponer de elementos comunes, como las mismas opciones en el men de la aplicacin, un men de Ayuda, etc. (Hom, 1996).
    p.mb-5(data-aos="fade-right") En la mayora de los casos la inspeccin debe llevarse a cabo por un experto en el estndar. En un determinado entorno, se podra exigir que la inspeccin la llevar a cabo alguien representativo de la compaa productora. A continuacin, se presenta cundo aplicar la tcnica de inspeccin de estndares para optimizar la usabilidad y eficiencia en el desarrollo de productos:
    .row
      .col-12.bg-c25(data-aos="fade-down")
        .p-4
          .row.justify-content-center
            .col-lg-4.bg-c22
              .p-4
                p.text-center.mb-0 #[b Cundo usar la tcnica de inspeccin de estndares?]
          .row.justify-content-center
            .col-md-8.col-xl.m-2
              .p-4
                figure.mb-4
                  img(src='@/assets/curso/tema1/img32.svg', style="width:250px" alt='').m-auto
                .row
                  .col-12.bg-c26
                    .p-4
                      p.mb-0 #[b Ideal para etapas intermedias:] se recomienda su uso en momentos centrales del desarrollo, cuando el diseo est en proceso y se ajusta a los estndares relevantes.
            .col-md-8.col-xl.m-2
              .p-4
                figure.mb-4
                  img(src='@/assets/curso/tema1/img33.svg', style="width:250px" alt='').m-auto
                .row 
                  .col-12.bg-c26
                    .p-4
                      p.mb-0 #[b A partir de prototipos:] inicia con prototipos de baja fidelidad. Sin embargo, es ms efectivo cuando se aplica a prototipos #[i software] avanzados o, incluso mejor, a una versin inicial del sistema final.
            .col-md-8.col-xl.m-2
              .p-4
                figure.mb-4
                  img(src='@/assets/curso/tema1/img34.svg', style="width:250px" alt='').m-auto
                .row 
                  .col-12.bg-c26
                    .p-4
                      p.mb-0 #[b Mejora continua:] facilita la identificacin temprana de reas de mejora, asegurando que el diseo final cumpla con los ms altos estndares de usabilidad y experiencia de usuario.
            .col-md-8.col-xl.m-2
              .p-4
                figure.mb-4
                  img(src='@/assets/curso/tema1/img35.svg', style="width:250px" alt='').m-auto
                .row 
                  .col-12.bg-c26
                    .p-4
                      p.mb-0 #[b Eficiencia en el desarrollo:] al aplicar esta tcnica desde fases tempranas, se reduce el tiempo y esfuerzo necesarios para realizar ajustes significativos en etapas posteriores, optimizando as el proceso de desarrollo.
    Separador 
