    .titulo-segundo.color-acento-contenido
      h2 1.2 Mtodos de indagacin
    p.mb-5(data-aos="fade-right") El proceso de indagacin facilita el conocimiento de un tema mediante el uso de conjeturas y seales. Este tipo de mtodos de evaluacin se basa en una premisa fundamental: #[b interactuar con los usuarios y observarlos cuidadosamente mientras utilizan el sistema en su entorno real]. De esta manera, se consiguen respuestas a las preguntas formuladas, ya sea verbalmente o por escrito.
    .row.mb-4
      .col-auto.bg-c6(data-aos="fade-down")
        .px-3.pb-0
          .row.align-items-end
            .col-auto.pe-0
              figure.fig-p1
                img(src='@/assets/curso/tema1/img5.svg', style="width:50px" alt='').m-auto
            .col
              h3 Cuestionarios
    .row.mb-4
      .col-lg-8.order-2.order-lg-1(data-aos="fade-right")
        p El trmino cuestionario se presenta como una lista de cuestiones o preguntas planteadas sobre un tema en especfico con la finalidad de que alguien las responda.
        p En el contexto de la evaluacin de usabilidad de sistemas interactivos #[i software] se hace referencia a cuestionarios como listas de preguntas que el evaluador distribuye entre usuarios y/o implicados para que estos devuelvan las respuestas y, de este modo, obtener conclusiones.
        .row
          .col-12.bg-c4
            .p-4
              p.mb-0 El cuestionario normalmente se distribuye en formato escrito y las preguntas plantean aspectos asociados con el sistema o aplicacin concreta. #[b En ese sentido, el objetivo del cuestionario es la recoleccin de informacin a partir de respuestas contestadas por los usuarios y/o los implicados].
      .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img29.png', style="width:400px" alt='').m-auto
    p.mb-5(data-aos="fade-right") Los tipos de preguntas a incluir en un cuestionario, segn Granollers son:
    .row.bg-c21.mb-5
      .col-12
        .p-5
          .tarjeta.tarjeta--azul1(data-aos="fade-down")
            .p-4.p-lg-5
              SlyderA(tipo="b")
                .row.justify-content-center.align-items-center
                  .col-lg-5.order-2.order-lg-1
                    p.txt-c2 #[b Pregunta de carcter general]
                    p.text-white Preguntas que aportan la caracterizacin del usuario como el perfil de usuario y su rol dentro de la poblacin en estudio. 
                    p.text-white.mb-0 Por ejemplo, puede incluir aspectos como edad, sexo, ocupacin, lugar de residencia, aficiones, estudios, aficiones, etc.
                  .col-lg-5.mb-4.mb-lg-0.order-1.order-lg-2
                    figure
                      img(src='@/assets/curso/tema1/img31.png',style="width:450px", alt='').m-auto
                .row.justify-content-center.align-items-center
                  .col-lg-5.order-2.order-lg-1
                    p.txt-c2 #[b Pregunta abierta]
                    p.text-white.mb-0 Tipo de pregunta para recoger informacin general subjetiva. Por lo general, da sugerencias interesantes y viabiliza encontrar errores no previstos.
                  .col-lg-5.mb-4.mb-lg-0.order-1.order-lg-2
                    figure
                      img(src='@/assets/curso/tema1/img32.png',style="width:450px", alt='').m-auto
                .row.justify-content-center.align-items-center
                  .col-lg-5.order-2.order-lg-1
                    p.txt-c2 #[b Pregunta de tipo escalar]
                    p.text-white.mb-0 Posibilita preguntas al usuario sobre un aspecto especfico en una escala numrica.
                  .col-lg-5.mb-4.mb-lg-0.order-1.order-lg-2
                    figure
                      img(src='@/assets/curso/tema1/img33.png',style="width:450px", alt='').m-auto
                .row.justify-content-center.align-items-center
                  .col-lg-5.order-2.order-lg-1
                    p.txt-c2 #[b Opcin mltiple]
                    p.text-white.mb-0 En este caso se ofrece una serie de opciones y se solicita responder a una o varias. Especialmente tiles para recoger informacin de la experiencia previa del usuario.
                  .col-lg-5.mb-4.mb-lg-0.order-1.order-lg-2
                    figure
                      img(src='@/assets/curso/tema1/img34.png',style="width:450px", alt='').m-auto
                .row.justify-content-center.align-items-center
                  .col-lg-5.order-2.order-lg-1
                    p.txt-c2 #[b Preguntas ordenadas]
                    p.text-white.mb-0 Se presentan como una serie de opciones que se debe ordenar.
                  .col-lg-5.mb-4.mb-lg-0.order-1.order-lg-2
                    figure
                      img(src='@/assets/curso/tema1/img35.png',style="width:450px", alt='').m-auto
    .row.mb-4
      .col-auto.bg-c6(data-aos="fade-down")
        .px-3.pb-0
          .row.align-items-end
            .col
              h3.mt-3 Partes de un cuestionario
    p(data-aos="fade-right") La actividad de la realizacin de cuestionarios est relacionada a la consecucin de ciertas tareas que el evaluador considera conveniente realizar (actividad combinada de varios mtodos de evaluacin) con el objeto de medir aspectos interactivos del sistema. 
    p.mb-5(data-aos="fade-right") En estos casos se sugiere dividir el cuestionario en tres partes:
    .row.justify-content-center.mb-5
      .col-md-10.col-lg.bg-c27.m-2(data-aos="fade-right")
        .p-4
          figure.mb-3
            img(src='@/assets/curso/tema1/img36.svg', style="width:80px" alt='')
          .row 
            .col-auto
              p.txt-c1 #[b Pretarea]
          p.mb-0 Las preguntas de esta parte por lo regular suelen ser generales acerca de las habilidades del usuario (se aprovecha para recoger informacin til acerca del perfil del usuario).
      .col-md-10.col-lg.bg-c27.m-2(data-aos="fade-down")
        .p-4
          figure.mb-3
            img(src='@/assets/curso/tema1/img37.svg', style="width:80px" alt='')
          .row 
            .col-auto
              p.txt-c1 #[b Postarea]
          p.mb-0 Esta seccin es iterativa y se harn tantas veces como tareas tenga que resolver el usuario.
      .col-md-10.col-lg.bg-c27.m-2(data-aos="fade-left")
        .p-4
          figure.mb-3
            img(src='@/assets/curso/tema1/img38.svg', style="width:80px" alt='')
          .row 
            .col-auto
              p.txt-c1 #[b #[i Postest]]
          p.mb-0 Se recogen aspectos generales acerca de la percepcin global u holstica del usuario tras la consecucin de las diferentes tareas propuestas.
    .row.mb-4
      .col-auto.bg-c6(data-aos="fade-down")
        .px-3.pb-0
          .row.align-items-end
            .col-auto.pe-0
              figure.fig-p1
                img(src='@/assets/curso/tema1/img5.svg', style="width:50px" alt='').m-auto
            .col
              h3 Grabacin del uso
    p(data-aos="fade-right") Esta tcnica, tambin conocida como anlisis de #[i logs] o simplemente #[i logging], se fundamenta en "grabar" o "recoger" todas las actividades realizadas por el usuario durante su interaccin con el sistema de #[i software] para su anlisis posterior. Por lo tanto, es necesaria una aplicacin secundaria que realice automticamente esta tarea de capturar absolutamente todo lo que el usuario hace. Cabe destacar que esta aplicacin es transparente para el usuario.
    .row.align-items-center.mb-4
      .col-lg-4.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema1/img36.png', style="width:350px" alt='').m-auto
      .col-lg-8(data-aos="fade-left")
        p En ese sentido, esta aplicacin que corre en el #[i background] recoger automticamente estadsticas sobre el uso detallado del sistema #[i software] a evaluar. Por lo tanto, esta tcnica es muy til porque muestra cmo los usuarios realizan su trabajo real y, a su vez, por la facilidad de capturar automticamente datos de una gran cantidad de usuarios.
        .row
          .col-12.bg-c4
            .p-4
              p.mb-0 El #[i log] o registro contendr estadsticas sobre la frecuencia con la que cada usuario ha utilizado cada caracterstica del programa y la frecuencia con la que han ocurrido diversos eventos de inters, como mensajes de error. As, se generar informacin valiosa para comprender aspectos de la aplicacin que podran haber pasado desapercibidos para el equipo de desarrollo.
    .row.justify-content-center.mb-5
      .col-lg-10.bg-c22(data-aos="fade-up")
        .p-4
          p Segn Granollers (2014), la estadstica:
          .row
            .col-lg-auto.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img39.svg', style="width:180px" alt='').m-auto
            .col
              p.mb-0 La frecuencia de uso de comandos y otras caractersticas del sistema puede utilizarse para optimizar aquellas caractersticas que se usan frecuentemente y para identificar aquellas que no se utilizan o se utilizan raramente. Las estadsticas que muestran la frecuencia de diversas situaciones de error y el uso de la ayuda pueden emplearse para mejorar la usabilidad del sistema, ajustando las caractersticas que causan la mayora de los errores y los accesos ms frecuentes a la ayuda en lnea.
    .row.mb-4
      .col-auto.bg-c6(data-aos="fade-down")
        .px-3.pb-0
          .row.align-items-end
            .col
              h3.mt-3 Procedimiento
    p.mb-5(data-aos="fade-right") El registro se realiza generalmente instalando el #[i software] que va a capturar todas las acciones del usuario. Es importante la configuracin de este tipo de #[i software] para que se centre en la aplicacin que se est probando.  Este ltimo mtodo suele ser el preferido, ya que hace ms fcil registrar acontecimientos de inters.
    .row.mb-4
      .col-auto.bg-c6(data-aos="fade-down")
        .px-3.pb-0
          .row.align-items-end
            .col
              h3.mt-3 Caractersticas
    p.mb-5(data-aos="fade-right") Las principales caractersticas que definen este mtodo son:
    .row
      .col-12.bg-c29(data-aos="fade-down")
        .p-4.p-lg-5
          .row.justify-content-center
            .col-md-8.col-lg.bg-c28.m-2
              .p-4
                p.text-center.mb-0 Es un mtodo muy econmico, dado que se puede realizar con un nmero alto de usuarios al mismo costo.
            .col-md-8.col-lg.bg-c28.m-2
              .p-4
                p.text-center.mb-0 No es necesaria la presencia fsica de los usuarios en un lugar predeterminado.
            .col-md-8.col-lg.bg-c28.m-2
              .p-4
                p.text-center.mb-0 Puede realizarse remotamente, lo que permite evaluar un gran nmero de datos.
            .col-md-8.col-lg.bg-c28.m-2
              .p-4
                p.text-center.mb-0 Los datos suelen tener un formato estndar, lo que facilita a la comparacin de datos segn diferentes criterios.
