    .titulo-segundo.color-acento-contenido
      h2 1.3 Mtodos de tipo #[i test]
    p.mb-5(data-aos="fade-right") Los mtodos de usabilidad tipo #[em test] implican que los usuarios realicen tareas utilizando el sistema o el prototipo (ya sea un #[i wireframe] o #[i mockup]), mientras los evaluadores analizan los resultados para determinar cmo la interfaz de usuario de la aplicacin apoya a los usuarios en la realizacin de sus tareas.
    .row.mb-4
      .col-auto.bg-c6(data-aos="fade-down")
        .px-3.pb-0
          .row.align-items-end
