              h3 Medida de las prestaciones
    .row.mb-5
      .col-12.bg-c2(data-aos="fade-left")
        .p-4
          .row.align-items-center
            .col-lg-auto.pe-0.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img37.png', style="width:350px", alt='').m-auto
            .col.bg-c3
              .p-4
                p.mb-0 Este mtodo de evaluacin est basado en medir de forma cualitativa y cuantitativa el rendimiento y las caractersticas subjetivas que afecte a la usabilidad del sistema, por lo que es imperativo que se tenga del sistema ya terminado o de un prototipo con un grado amplio de desarrollo que permita evaluar estos aspectos.
    .row.mb-4
      .col-auto.bg-c6(data-aos="fade-down")
        .px-3.pb-0
          .row.align-items-end
            .col
              h3.mt-3 Caractersticas
    p.mb-4(data-aos="fade-right") Las principales caractersticas que definen este mtodo son:
    .row.justify-content-center.mb-4
      .col-lg-10
        .row.mb-3
          .col-12.bg-c30(data-aos="fade-right")
            .p-3
              .row.align-items-center
                .col-md-auto.mb-4.mb-md-0
                  figure
                    img(src='@/assets/curso/tema1/img41.svg', style="width:120px" alt='').m-auto
                .col
                  p.mb-0 El objetivo de toda tcnica es procurar la mejora de la usabilidad del producto; no debe confundirse con un #[i test] de funcionalidad, pues sus intenciones o finalidades son diferentes porque se centra en garantizar que el producto funcione de acuerdo con las especificaciones.
        .row.mb-3
          .col-12.bg-c31(data-aos="fade-down")
            .p-3
              .row.align-items-center
                .col-md-auto.mb-4.mb-md-0
                  figure
                    img(src='@/assets/curso/tema1/img42.svg', style="width:120px" alt='').m-auto
                .col
                  p.mb-0 Los participantes (presenciales) en el #[i test] deben analizar cmo utiliza el producto y el tiempo que demoran realizando tareas.
        .row.mb-3
          .col-12.bg-c30(data-aos="fade-left")
            .p-3
              .row.align-items-center
                .col-md-auto.mb-4.mb-md-0
                  figure
                    img(src='@/assets/curso/tema1/img43.svg', style="width:120px" alt='').m-auto
                .col
                  p.mb-0 Como recomendacin es hacer esta tcnica en un laboratorio de usabilidad. Para la realizacin del #[em test] hay que definir las tareas que los usuarios deben realizar.
    p.mb-4(data-aos="fade-right") Aspectos para tener en cuenta a la hora de escoger las tareas:
    .row.align-items-center.mb-4
      .col-lg-8.order-2.order-lg-1(data-aos="fade-right")
        .row
          .col-12
            .bg-c10
              .p-4
                ul.lista-ul
                  li.d-flex
                    i.fas.fa-circle(style="color:#1682FB !important")
                    | Tareas que demuestren problemas de usabilidad.
                  li.d-flex
                    i.fas.fa-circle(style="color:#1682FB !important")
                    | Tareas recomendadas por la experiencia.
                  li.d-flex
                    i.fas.fa-circle(style="color:#1682FB !important")
                    | Tareas derivadas de otros criterios como pueden ser las tareas que son difciles de recuperar despus de un error.
                  li.d-flex
                    i.fas.fa-circle(style="color:#1682FB !important")
                    | Tareas que los usuarios realizarn con el producto.
                  li.d-flex
                    i.fas.fa-circle(style="color:#1682FB !important")
                    | Funcionamiento del mtodo.
      .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img38.png', style="width:450px" alt='').m-auto
    p.mb-4(data-aos="fade-right") Es necesario comprender qu se puede medir. Se requiere recoger:
    .row.justify-content-center.mb-5
      .col-md-8.col-lg-5.bg-c9.m-2(data-aos="fade-right")
        .p-4
          figure.mb-3
            img(src='@/assets/curso/tema1/img44.svg', style="width:100px" alt='')
          .row 
            .col-auto
              p.txt-c3 Medidas de rendimiento
          p.text-white.mb-0 Importante contar las acciones y comportamientos. Las medidas son de tipo cuantitativas, por lo que se puede contar con personas, nmero de errores cometidos, nmero de veces que repiten el mismo error.
      .col-md-8.col-lg-5.bg-c9.m-2(data-aos="fade-left")
        .p-4
          figure.mb-3
            img(src='@/assets/curso/tema1/img45.svg', style="width:100px" alt='')
          .row 
            .col-auto
              p.txt-c3 Medidas subjetivas
          p.text-white.mb-0 Son las percepciones de los usuarios, juicios y comentarios. Pueden ser de tipo cualitativo o cuantitativo.
    .row.mb-4
      .col-auto.bg-c6(data-aos="fade-down")
        .px-3.pb-0
          .row.align-items-end
            .col-auto.pe-0
              figure.fig-p1
                img(src='@/assets/curso/tema1/img5.svg', style="width:50px" alt='').m-auto
            .col
              h3 Pensando en voz alta (#[i Thinking Aloud])
    p.mb-4(data-aos="fade-right") Es uno de los mtodos ms longevos dentro del contexto de la usabilidad. Se encarga de pedir a los participantes de la prueba que usen el sistema mientras piensan en voz alta continuamente, es decir, #[b simplemente] verbalizan sus pensamientos mientras se mueven por la interfaz de usuario.
    .row.justify-content-center.mb-4
      .col-10.bg-c8(data-aos="fade-down")
        .p-4
          .row.align-items-center
            .col-md-auto.mb-4.mb-md-0
              figure
                img(src='@/assets/curso/tema1/img46.svg', style="width:100px" alt='').m-auto
            .col
              p.mb-0 La palabra simplemente est entrecomillada, porque no es tan simple para la mayora de las personas mantener un monlogo activo. El facilitador o evaluador de la prueba generalmente tiene que exhortar a los usuarios a que sigan hablando.
    p.mb-4(data-aos="fade-right") Para ejecutar un estudio de usabilidad de pensamiento bsico en voz alta, solo se necesitan hacer tres cosas:
    .row.align-items-center.mb-4
      .col-lg-8.order-2.order-lg-1(data-aos="fade-right")
        .row
          .col-12.bg-c11
            .p-4
              ul.lista-ul.mb-0
                li.d-flex
                  i.fas.fa-circle(style="color:#1682FB !important")
                  | Reclutar usuarios representativos (pblico objetivo).
                li.d-flex
                  i.fas.fa-circle(style="color:#1682FB !important")
                  | Darles tareas representativas para realizar.
                li.d-flex
                  i.fas.fa-circle(style="color:#1682FB !important")
                  | Hacer silencio y dejar que los usuarios hablen.
      .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img39.png', style="width:350px" alt='').m-auto
    p.mb-4(data-aos="fade-right") Ahora, se vern los beneficios de este mtodo:
    TabsB.color-acento-contenido.mb-5(data-aos="fade-left")
      .py-4.py-md-5(titulo="Perspectiva de diseo" :icono="require('@/assets/curso/tema1/img47.svg')")
        .row.align-items-center.justify-content-center
          .col-lg-6.order-2.order-lg-1
            p #[b Perspectiva de diseo]
            p Permite descubrir lo que los usuarios realmente piensan sobre el diseo. En particular, escuchar sus conceptos errneos que, generalmente, se convierten en recomendaciones de rediseo.
          .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2
            figure
              img(src='@/assets/curso/tema1/img52.svg', style="width:250px", alt='').m-auto
      .py-4.py-md-5(titulo="Adaptacin de interfaz" :icono="require('@/assets/curso/tema1/img48.svg')")
        .row.align-items-center.justify-content-center
          .col-lg-6.order-2.order-lg-1
            p #[b Adaptacin de interfaz]
            p Cuando los usuarios malinterpretan elementos de diseo, se deber hacer el cambio. An mejor, generalmente se aprender por qu los usuarios adivinan mal sobre algunas partes de la interfaz de usuario y por qu encuentran otras fciles de usar.
          .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2
            figure
              img(src='@/assets/curso/tema1/img53.svg', style="width:250px", alt='').m-auto
      .py-4.py-md-5(titulo="Barato" :icono="require('@/assets/curso/tema1/img49.svg')")
        .row.align-items-center.justify-content-center
          .col-lg-6.order-2.order-lg-1
            p #[b Barato]
            p No se necesita equipo especial; simplemente es sentarse junto a un usuario y tomar notas mientras habla. Se tarda aproximadamente un da en recopilar datos de un puado de usuarios, que es todo lo que se necesita para obtener los conocimientos ms importantes.
          .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2
            figure
              img(src='@/assets/curso/tema1/img54.svg', style="width:250px", alt='').m-auto
      .py-4.py-md-5(titulo="Flexible" :icono="require('@/assets/curso/tema1/img50.svg')")
        .row.align-items-center.justify-content-center
          .col-lg-6.order-2.order-lg-1
            p #[b Flexible]
            p Puede utilizar el mtodo en cualquier etapa del ciclo de vida del desarrollo, desde los primeros prototipos en papel  hasta los sistemas en ejecucin completamente implementados.
          .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2
            figure
              img(src='@/assets/curso/tema1/img55.svg', style="width:250px", alt='').m-auto
      .py-4.py-md-5(titulo="Convincente" :icono="require('@/assets/curso/tema1/img51.svg')")
        .row.align-items-center.justify-content-center
          .col-lg-6.order-2.order-lg-1
            p #[b Convincente]
            p Lograr que el resto de su equipo (y la gerencia) se siente en algunas sesiones de pensamiento en voz alta, no les toma mucho tiempo y es la mejor manera de motivarlos para que presten atencin a la usabilidad.
          .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2
            figure
              img(src='@/assets/curso/tema1/img56.svg', style="width:250px", alt='').m-auto
    .row.mb-4
      .col-auto.bg-c6(data-aos="fade-down")
        .px-3.pb-0
          .row.align-items-end
            .col-auto.pe-0
              figure.fig-p1
                img(src='@/assets/curso/tema1/img5.svg', style="width:50px" alt='').m-auto
            .col
              h3 Mtodo del conductor
    p(data-aos="fade-right") Este mtodo difiere con los anteriormente vistos, donde el evaluador o facilitador no interfiere en las acciones del usuario. Por lo tanto, este mtodo tiene una dinmica muy parecida al anterior, pero con la salvedad que se conduce al usuario en la direccin correcta mientras se usa el sistema.
    .row.mb-5
      .col-lg-4.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema1/img40.png', style="width:390px" alt='').m-auto
      .col-lg-8(data-aos="fade-left")
        p.mb-0 Durante la prueba, el usuario puede preguntar al evaluador/facilitador del #[em test] cualquier cuestin relacionada con el sistema y a su vez este deber responder. En ese sentido, el mtodo se enfoca en el usuario inexperto y/o con poca afinidad tecnolgica y el propsito de este, es descubrir las necesidades de informacin de los usuarios de tal forma que se genere un mejor entrenamiento y documentacin al mismo tiempo que un posible rediseo de la interfaz.
    .row.mb-4
      .col-auto.bg-c6(data-aos="fade-down")
        .px-3.pb-0
          .row.align-items-end
            .col-auto.pe-0
              figure.fig-p1
                img(src='@/assets/curso/tema1/img5.svg', style="width:50px" alt='').m-auto
            .col
              h3 #[i Card Sorting]
    .row.mb-5
      .col-lg-8.order-2.order-lg-1(data-aos="fade-right")
        p Parte de hacer que un sitio sea fcil de usar es organizar la informacin para que las personas encuentren lo que buscan. En ese sentido, #[i Card Sorting] o clasificacin de tarjetas es un mtodo de investigacin asociado a la disciplina de la Experiencia de Usuario (UX), en la que los participantes del estudio agrupan etiquetas individuales escritas en tarjetas de notas de acuerdo con los criterios que tienen sentido para ellos.
        .row
          .col-12.bg-c4
            .p-4
              p.mb-0 Este mtodo permite descubrir cmo est estructurado el conocimiento del dominio de la audiencia objetivo y se utiliza para crear una arquitectura de informacin que se alinea con las expectativas de los usuarios. A continuacin, se presenta un ejemplo:
      .col-lg-4.mb-4.mb-lg-0.order-1.order-lg-2(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img41.png', style="width:390px" alt='').m-auto
    figure.mb-4(data-aos="fade-down")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/LIltBJU60Vw?si=uKLWmNmUFY3v4m-f" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
    p.mb-4(data-aos="fade-right") Generalmente, el proceso funciona de la siguiente manera:
    .row.bg-c21(data-aos="fade-left")
      .col-12
        .p-5
          .tarjeta.tarjeta--azul1
            .py-2.px-4
              SlyderA(tipo="b")
                .row.justify-content-center.align-items-center.m-1
                  .col-lg-7.order-2.order-lg-1
                    .row
                      .col-auto
                        p.txt-c4 #[b Paso 1]
                    .row
                      .col-auto
                        p.txt-c3 #[b Elegir un conjunto de temas]
                    p.text-white El conjunto debe incluir entre 40 y 80 elementos que representen el contenido principal del sitio. Escribir cada tema en una ficha individual.
                    p.text-white.mb-0 #[b Consejo]
                    o.text-white Evitar los temas que contengan las mismas palabras; los participantes tendern a agrupar esas tarjetas.
                  .col-lg-3.mb-4.mb-lg-0.order-1.order-lg-2
                    figure
                      img(src='@/assets/curso/tema1/img42.png',style="width:450px", alt='').m-auto
                .row.justify-content-center.align-items-center.m-1
                  .col-lg-9.order-2.order-lg-1
                    .row
                      .col-auto
                        p.txt-c4 #[b Paso 2]
                    .row
                      .col-auto
                        p.txt-c3 #[b El usuario organiza los temas en grupos]
                    p.text-white Baraje las cartas y entrguelas al participante. Pdales que examinen las tarjetas una por una y las agrupen segn vayan juntas en pilas. Algunas pilas pueden ser grandes, otras pequeas. Si el participante no est seguro acerca de una tarjeta o no sabe lo que significa, est bien dejarla a un lado. Es preferible tener un conjunto de tarjetas "desconocidas" o "inseguras" que agruparlas al azar.
                    p.text-white.mb-0 #[b Notas]
                    ul.lista-ul.mb-0
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        |
                        p.text-white.mb-0 No hay un nmero preestablecido de pilas; puede ocurrir que haya montones pequeos o grandes.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        |
                        p.text-white.mb-0 Todo depende de los modelos mentales individuales.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        |
                        p.text-white.mb-0 Los participantes deben saber que est bien cambiar de opinin mientras trabajan: pueden mover una carta de una pila a otra, fusionar dos pilas, dividir una pila en varias pilas nuevas, etc.
                      li.d-flex
                        i.fas.fa-circle(style="color:#1682FB !important")
                        |
                        p.text-white.mb-0 La clasificacin de tarjetas es un proceso de abajo hacia arriba y es de esperar que se cometan errores iniciales.
                  .col-lg-3.mb-4.mb-lg-0.order-1.order-lg-2
                    figure
                      img(src='@/assets/curso/tema1/img43.png',style="width:450px", alt='').m-auto
                .row.justify-content-center.align-items-center.m-1
                  .col-lg-7.order-2.order-lg-1
                    .row
                      .col-auto
                        p.txt-c4 #[b Paso 3]
                    .row
                      .col-auto
                        p.txt-c3 #[b Nombres de usuario de los grupos]
                    p.text-white Una vez que el usuario haya agrupado todas las tarjetas a su satisfaccin, ahora se deben proporcionar tarjetas en blanco y solicitar que escriban un nombre para cada grupo que crearon. Este paso revelar el modelo mental del usuario sobre el espacio temtico. Se pueden obtener algunas ideas para las categoras de navegacin, pero no se debe esperar que los participantes creen etiquetas efectivas.
                  .col-lg-3.mb-4.mb-lg-0.order-1.order-lg-2
                    figure
                      img(src='@/assets/curso/tema1/img44.png',style="width:450px", alt='').m-auto
                .row.justify-content-center.align-items-center.m-1
                  .col-lg-7.order-2.order-lg-1
                    .row
                      .col-auto
                        p.txt-c4 #[b Paso 4]
                    .row
                      .col-auto
                        p.txt-c3 #[b Informar al usuario]
                    p.text-white (Este paso es opcional, pero muy recomendable). Pida a los usuarios que expliquen el fundamento de los grupos que crearon. Como buena prctica, se les puede pedir a los usuarios que piensen en voz alta mientras realizan la clasificacin, ya que esto puede proporcionar informacin detallada.
                  .col-lg-3.mb-4.mb-lg-0.order-1.order-lg-2
                    figure
                      img(src='@/assets/curso/tema1/img45.png',style="width:450px", alt='').m-auto
                .row.justify-content-center.align-items-center.m-1
                  .col-lg-7.order-2.order-lg-1
                    .row
                      .col-auto
                        p.txt-c4 #[b Paso 5]
                    .row
                      .col-auto
                        p.txt-c3 #[b Si es necesario]
                    p.text-white Solicite al usuario tamaos de grupo ms prcticos. No se deben imponer los propios deseos o prejuicios al participante durante la clasificacin original (pasos 1 a 3), pero una vez que se haya definido la agrupacin preferida del usuario y despus del informe inicial, definitivamente se puede solicitar al usuario o participante que divida grupos grandes en subgrupos ms pequeos. O al contrario: agrupar pequeos grupos en categoras ms grandes.
                  .col-lg-3.mb-4.mb-lg-0.order-1.order-lg-2
                    figure
                      img(src='@/assets/curso/tema1/img46.png',style="width:450px", alt='').m-auto
                .row.justify-content-center.align-items-center.m-1
                  .col-lg-7.order-2.order-lg-1
                    .row
                      .col-auto
                        p.txt-c4 #[b Paso 6]
                    .row
                      .col-auto
                        p.txt-c3 #[b Repetir con 15 a 20 usuarios]
                    p.text-white Se necesitarn suficientes usuarios para detectar patrones en los modelos mentales de los usuarios. Se recomienda contar con 15 participantes para la clasificacin de tarjetas.
                  .col-lg-3.mb-4.mb-lg-0.order-1.order-lg-2
                    figure
                      img(src='@/assets/curso/tema1/img47.png',style="width:450px", alt='').m-auto
                .row.justify-content-center.align-items-center.m-1
                  .col-lg-7.order-2.order-lg-1
                    .row
                      .col-auto
                        p.txt-c4 #[b Paso 7]
                    .row
                      .col-auto
                        p.txt-c3 #[b Analizar los datos]
                    p.text-white Una vez que tenga todos los datos, busque grupos comunes, nombres de categoras o temas, y elementos que se emparejan con frecuencia. En el caso de que algunos elementos se hayan dejado a un lado con frecuencia, hay que determinar si es porque las etiquetas de las tarjetas no eran claras o si el contenido no pareca estar relacionado con el resto de los temas. Combinar los patrones que estn relacionados con los conocimientos cualitativos del informe lo colocar en una mejor posicin para comprender qu sistema de organizacin ser ms exitoso para los usuarios.
                  .col-lg-3.mb-4.mb-lg-0.order-1.order-lg-2
                    figure
                      img(src='@/assets/curso/tema1/img48.png',style="width:450px", alt='').m-auto
      
